import { useEffect, useState } from "react";
import { history } from "@umijs/max";
import { Modal } from "antd";
import { db } from "@/services/db";

export const useApiKey = () => {
  const [apiKey, setApiKey] = useState<string>("");
  const [model, setModel] = useState<string>("deepseek");
  const [isLoading, setIsLoading] = useState<boolean>(true);

  /**
   * åˆ¤æ–­æ˜¯å¦éœ€è¦æ˜¾ç¤ºAlertæç¤º
   * @returns å¦‚æžœéœ€è¦æ˜¾ç¤ºAlertè¿”å›žtrueï¼Œå¦åˆ™è¿”å›žfalse
   */
  const shouldShowAlert = !apiKey;

  /**
   * è·³è½¬åˆ°AIè®¾ç½®é¡µé¢
   */
  const navigateToSettings = () => {
    history.push("/ai-setting");
  };

  /**
   * æ£€æŸ¥API Keyæ˜¯å¦å·²é…ç½®
   * @returns å¦‚æžœAPI Keyå·²é…ç½®è¿”å›žtrueï¼Œå¦åˆ™è¿”å›žfalse
   */
  const checkApiKey = (): boolean => {
    if (!apiKey) {
      Modal.confirm({
        title: "å°šæœªè®¾ç½® AI API Key",
        content: "è®¾ç½®å®ŒæˆåŽå³å¯ä½¿ç”¨è¯¥åŠŸèƒ½ï¼Œæ˜¯å¦çŽ°åœ¨åŽ»è®¾ç½®ï¼Ÿ",
        okText: "åŽ»è®¾ç½®",
        cancelText: "å–æ¶ˆ",
        onOk: navigateToSettings,
      });
      return false;
    }
    return true;
  };

  /**
   * èŽ·å–å½“å‰é…ç½®çš„API Keyå’Œæ¨¡åž‹
   * @returns åŒ…å«apiKeyå’Œmodelçš„å¯¹è±¡
   */
  const getApiKeyConfig = () => {
    return {
      apiKey,
      model,
    };
  };

  // åŠ è½½æŒ‡å®šæ¨¡åž‹çš„API Key
  const loadApiKeyForModel = async (targetModel: string) => {
    try {
      console.log("Loading API Key for model:", targetModel);
      const userApiKeys = await db.getUserApiKeys(1);

      // æŸ¥æ‰¾æŒ‡å®šæ¨¡åž‹çš„API Key
      const modelApiKey = userApiKeys.find((key) => key.model === targetModel);
      console.log("Found API Key for model", targetModel, ":", modelApiKey);

      setApiKey(modelApiKey?.api_key || "");
      setModel(targetModel);

      // å°†æ‰€æœ‰API Keyçš„isCurrentè®¾ç½®ä¸ºfalseï¼Œç„¶åŽå°†æŒ‡å®šæ¨¡åž‹çš„API Keyè®¾ç½®ä¸ºå½“å‰
      console.log("Updating current API Key status for model", targetModel);
      for (const key of userApiKeys) {
        if (key.id) {
          await db.updateApiKey(key.id, {
            is_current: key.model === targetModel,
          });
        }
      }
    } catch (error) {
      console.error("Failed to load API Key for model:", targetModel, error);
      setApiKey("");
      setModel(targetModel);
    }
  };

  // åŠ è½½åˆå§‹API Keyå’Œæ¨¡åž‹
  useEffect(() => {
    const loadInitialApiKeyAndModel = async () => {
      try {
        // é¦–å…ˆå°è¯•èŽ·å–å½“å‰è®¾ç½®çš„API Key
        const currentApiKey = await db.getCurrentApiKey(1);

        if (currentApiKey) {
          setApiKey(currentApiKey.api_key);
          setModel(currentApiKey.model);
        } else {
          // å¦‚æžœæ²¡æœ‰å½“å‰API Keyï¼Œé»˜è®¤ä½¿ç”¨deepseekå¹¶å°è¯•åŠ è½½å…¶API Key
          console.log("No current API Key found, using default model deepseek");
          await loadApiKeyForModel("deepseek");
        }
      } catch (error) {
        console.error("Failed to load initial API Key and Model:", error);
      } finally {
        setIsLoading(false);
      }
    };

    loadInitialApiKeyAndModel();
  }, []);

  // åˆ‡æ¢æ¨¡åž‹çš„æ–¹æ³•
  const switchModel = async (newModel: string) => {
    if (newModel === model) return; // å¦‚æžœæ˜¯åŒä¸€ä¸ªæ¨¡åž‹ï¼Œä¸åšä»»ä½•æ“ä½œ

    setIsLoading(true);
    await loadApiKeyForModel(newModel);
    setIsLoading(false);
  };

  // ä¿å­˜ API Key å’Œæ¨¡åž‹ï¼ˆæš‚æ—¶ä½¿ç”¨ç”¨æˆ·ID 1ï¼Œå®žé™…åº”ç”¨ä¸­éœ€è¦ä»Žç”¨æˆ·ç™»å½•çŠ¶æ€èŽ·å–ï¼‰
  const saveApiKey = async (
    newApiKey: string,
    newModel: string = "deepseek"
  ) => {
    try {
      console.log("Saving API Key and Model:", {
        apiKey: newApiKey,
        model: newModel,
      });
      const savedApiKey = await db.createApiKey({
        user_id: 1,
        api_key: newApiKey,
        model: newModel,
        is_current: true,
      });
      console.log("Saved API Key and Model to DB:", savedApiKey);
      setApiKey(newApiKey);
      setModel(newModel);
      return true;
    } catch (error) {
      console.error("Failed to save API Key and Model:", error);
      return false;
    }
  };

  // åˆ é™¤ API Keyï¼ˆåˆ é™¤å½“å‰æ¨¡åž‹å¯¹åº”çš„API Keyï¼‰
  const deleteApiKey = async () => {
    try {
      console.log("Deleting API Key for model:", model);
      // èŽ·å–å½“å‰ç”¨æˆ·è¯¥æ¨¡åž‹å¯¹åº”çš„API Key
      const userApiKeys = await db.getUserApiKeys(1);
      const modelApiKey = userApiKeys.find((key) => key.model === model);

      if (modelApiKey?.id) {
        console.log("Deleting API Key with ID:", modelApiKey.id);
        await db.deleteApiKey(modelApiKey.id);
      }

      setApiKey("");
      console.log("API Key deleted successfully");
      return true;
    } catch (error) {
      console.error("Failed to delete API Key:", error);
      return false;
    }
  };

  // éªŒè¯API Keyæ ¼å¼ï¼ˆæ”¯æŒä¸åŒæ¨¡åž‹çš„API Keyæ ¼å¼ï¼‰
  const validateApiKey = (apiKeyToValidate: string) => {
    // DeepSeek API Keyæ ¼å¼ï¼šsk-å¼€å¤´çš„å­—ç¬¦ä¸²
    // Google Gemini API Keyæ ¼å¼ï¼šAIzaå¼€å¤´çš„å­—ç¬¦ä¸²
    // å°ç±³MiMo API Keyæ ¼å¼ï¼šsk-å¼€å¤´çš„å­—ç¬¦ä¸²
    const deepSeekRegex = /^sk-/;
    const geminiRegex = /^AIza/;
    const mimoRegex = /^sk-/;

    const trimmedApiKey = apiKeyToValidate.trim();
    return (
      deepSeekRegex.test(trimmedApiKey) ||
      geminiRegex.test(trimmedApiKey) ||
      mimoRegex.test(trimmedApiKey)
    );
  };

  return {
    apiKey,
    model,
    isLoading,
    shouldShowAlert,
    navigateToSettings,
    saveApiKey,
    deleteApiKey,
    validateApiKey,
    switchModel,
    checkApiKey,
    getApiKeyConfig,
  };
};
