import { injectable } from "tsyringe";
import { BaseAIProvider } from "./baseAIProvider";
import type { AIProviderRequest, AIProviderResponse } from "./baseAIProvider";
import { GoogleGenerativeAI } from "@google/generative-ai";
import {
  AIProviderType,
  getTemperatureByConfig,
} from "../../../config/aiTemperatureConfig";

/**
 * Gemini AI Provider
 * è´Ÿè´£è°ƒç”¨Gemini APIï¼Œè¿”å›žåŽŸå§‹å“åº”
 */
@injectable()
export class GeminiProvider extends BaseAIProvider {
  /**
   * è°ƒç”¨Gemini APIç”Ÿæˆå†…å®¹
   * @param request - AI Providerè¯·æ±‚å‚æ•°
   * @returns åŒ…å«åŽŸå§‹AIå“åº”çš„æ ‡å‡†åŒ–å“åº”
   */
  async generate(request: AIProviderRequest): Promise<AIProviderResponse> {
    this.validateRequest(request);

    const {
      api_key: apiKey,
      system_prompt: systemPrompt,
      user_prompt: userPrompt,
      business_type: businessType,
      temperature,
    } = request;

    // ä»Žé…ç½®æ–‡ä»¶èŽ·å–temperatureå‚æ•°ï¼Œå¦‚æžœrequestä¸­æä¾›äº†temperatureåˆ™ä¼˜å…ˆä½¿ç”¨
    const configTemperature = getTemperatureByConfig(
      businessType,
      AIProviderType.GEMINI
    );
    const finalTemperature =
      temperature !== undefined ? temperature : configTemperature;

    try {
      const genAI = new GoogleGenerativeAI(apiKey);

      // ä½¿ç”¨Google GenAI SDKçš„æ­£ç¡®æ–¹æ³•è°ƒç”¨
      const model = genAI.getGenerativeModel({
        model: "gemini-3-flash-preview",
        generationConfig: {
          temperature: finalTemperature,
        },
      });

      const result = await model.generateContent(
        systemPrompt + "\n\n" + userPrompt
      );
      const response = await result.response;
      const content = response.text();

      if (!content || !content.trim()) {
        return this.createErrorResponse("AI ç”Ÿæˆå†…å®¹ä¸ºç©º");
      }

      return this.createSuccessResponse(content);
    } catch (error) {
      const errorMessage = error instanceof Error ? error.message : "æœªçŸ¥é”™è¯¯";
      console.error("Gemini APIè°ƒç”¨å¤±è´¥:", error);
      return this.createErrorResponse(errorMessage);
    }
  }
}
